<template>
  <div class="finance-new-detail full-screen">
    <div class="page-panel">
      <pem-scroll-view 
        class="scroll-view"
        ref="scroll" 
        :pullup=true
        @pullUpLoad="listMore">
        <div class="container">
          <div class="title">{{ result.title }}</div>
          <div class="bar">
            <span class="report" v-if="result.tags && result.tags.length > 0">报告</span>
            <span v-if="result.type">来源: {{ result.type }}</span>
            <span class="time">{{ result.date }}</span>
          </div>
          <div class="abstract" v-if="result.brief">摘要：{{result.brief}}</div>
          <div class="content" v-html="result.contents"></div>
        </div>
      </pem-scroll-view>
      <pem-operation-panel v-if="showOperation" @backtotop="backtotop" />
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemScrollView from 'components/scroll-view/scroll-view'
import { MiniSiteApi } from 'api'
import { setTitle } from 'common/js/util'

export default {
  data () {
    return {
      showOperation: false,
      isMore: false,
      result: {}
    }
  },
  components: {
    PemOperationPanel,
    PemScrollView
  },
  activated () {
    let options = {id: this.$route.query.id}
    this._getFinanceNewDetail(options)
  },
  created () {
    if (this.$route.query.from === 'wap') {
      this.showOperation = true
    }
    this._initWXShare()
    this._setTitle('项目工场-头条详情')
  },
  methods: {
    _initWXShare () {
      let title = this.result.title || '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！'
      this.wxShare({ title })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _getFinanceNewDetail (options) {
      MiniSiteApi.getFinanceNewDetail(options).then(res => {
        this.result = res || {}
        this._initWXShare()
        this.$nextTick(function () {
          this.$refs.scroll.scrollTo(0, 0, 0)
          this.refresh()
        })
        if (this.result && (this.result.contents || '').indexOf('<img') > -1) {
          this.isMore = true
        }
      }).catch((error) => {
        console.error(error)
        // this.$router.push('/invalid')
      })
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    refresh () {
      this.$refs.scroll.refresh()
    },
    listMore () {
      if (this.isMore) {
        this.refresh()
      }
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .page-panel
    top: 0
  .finance-new-detail >>> p
    padding-bottom: 10px
  .container
    padding: 25px 15px 50px
    .title
      font-size: 20px
      font-weight: bold
      color: #41403E
      line-height: 26px
      padding-bottom: 10px
    .bar
      font-size: 12px
      color: #A7A5A3
      .report
        display: inline-block
        font-size: 11px
        color: #FFC133
        padding: 1px 3px
        margin-right: 7px
        border: 1px solid #FFC133;
        border-radius: 1px
        transform: scale(0.9)
      .time
        display: inline-block
        padding-left: 10px
    .abstract
      background: #F6F6F6
      padding: 11px 9px
      margin-top: 19px
      font-size: 14px
      line-height: 22px
      color: #474646
      letter-spacing: 0.26px
    .content
      padding-top: 20px
      font-size: 16px
      line-height: 26px
      color: #585755
</style>



